 <!--登录注册路由组件-->
<template>
  <div class="loginContainer">
    <div class="loginInner">
      <div class="login_header">
        <h2 class="login_logo">硅谷外卖</h2>
        <div class="login_header_title">
          <a href="javascript:;" :class="{on:loginType}" @click="loginType=true">短信登录</a>
          <a href="javascript:;" :class="{on:!loginType}" @click="loginType=false">密码登录</a>
        </div>
      </div>
      <div class="login_content">
        <form>
          <div :class="{on:loginType}">
            <section class="login_message">
              <input type="tel" maxlength="11" placeholder="手机号" v-model="phone">
              <!-- 根据输入的电话号码是否正确决定按钮是否可以点击 -->
              <button :disabled="!isrightPhone || countdown>0 " class="get_verification" :class="{right_phone_number:isrightPhone}" @click.prevent="sendCode">
                {{countdown>0 ? `已发送(${countdown}s)` : '获取验证码'}}
              </button>
            </section>
            <section class="login_verification">
              <input type="tel" maxlength="8" placeholder="验证码" v-model="code">
            </section>
            <section class="login_hint">
              温馨提示：未注册硅谷外卖帐号的手机号，登录时将自动注册，且代表已同意
              <a href="javascript:;">《用户服务协议》</a>
            </section>
          </div>
          <div :class="{on:!loginType}">
            <section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="手机/邮箱/用户名" v-model="name">
              </section>
              <section class="login_verification">
                <input :type="isShowPwd ? 'text' : 'password'" maxlength="8" placeholder="密码" v-model="pwd">
                <div class="switch_button off" @click="isShowPwd=!isShowPwd" :class="isShowPwd?'on':'off'">
                  <div class="switch_circle" :class="{right: isShowPwd}"></div>
                  <span class="switch_text">{{isShowPwd ? 'abc' : '...'}}</span>
                </div>
              </section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="验证码" v-model="captcha">
                <img class="get_verification" src="http://localhost:3000/captcha" alt="captcha"  
                    ref='captcha' @click="updateCaptcha">
              </section>
            </section>
          </div>
          <button class="login_submit" @click.prevent="login">登录</button>
        </form>
        <a href="javascript:;" class="about_us">关于我们</a>
      </div>
      <span href="javascript:" class="go_back" @click="$router.back()">
        <i class="iconfont icon-jiantou2"></i>
      </span>
    </div>
  </div>  
</template>

<script>
  import {Toast, MessageBox} from 'mint-ui'
  import {reqSendCode,reqSmsLogin,reqPwdLogin} from '../../api'

  export default {
    
    data () {
      return {
        loginType: true,//登录类型 true：手机短信 false：密码
        phone: '',//输入的手机号码
        code: '',//短信验证码
        name: '', // 用户名
        pwd: '', // 用户密码
        captcha: '', // 登录图形验证码        
        countdown: 0, //倒计时剩余时间 初始值为0，点击后开始倒计时
        isShowPwd: false, // 是否显示密码
      }
    },
    computed:{
      // 根据输入电话号码的值，计算输入电话号码是否正确
      isrightPhone () {
        // 返回布尔类型的值
        return /^1\d{10}$/.test(this.phone)
      }
    },
    methods:{
      //1：发送请求启动倒计时，获取短信验证码
      async sendCode () {
        // 1.启动倒计时
        // 定义倒计时时间为10s
        this.countdown = 10
        const intervalId = setInterval(()=>{
          if(this.countdown<=0) {
            // 清除定时器
            clearInterval(intervalId)
            return
          }
          this.countdown--
        },1000)

        //2. 获取短信验证码
        const result = await reqSendCode(this.phone)
        if (result.code===0) {
          //  alert('验证码已发送')
           Toast('验证码已发送')
        }else{
          this.countdown = 0
          // alert(result.msg)
          MessageBox.alert(result.msg).then(action => {
            console.log('点击确定')
          });
        }
      },
      // 2：更新图形验证码
      updateCaptcha () {
        //重新定义图形验证码地址
        this.$refs.captcha.src = 'http://localhost:3000/captcha?time='+Date.now()
      },
      // 3:点击按钮发送请求实现登录
      async login () {
        // 请求结束后, 停止倒计时
        // this.countdown = 0
        // this.code = ''
        // 请求结束更新验证码
        // this.captcha = ''
        // this.updateCaptcha()
        

        let result//接收发送异步请求的结果
        
        if (this.loginType) {//手机号登录
          // 前台表单验证
          const {phone,code} = this
          if (!this.isrightPhone) {
            MessageBox.alert('请输入正确手机号')
            return
          }else if (!/^\d{6}$/.test(code)) {
            return MessageBox.alert('请输入正确验证码')
          }
          // 发送请求获取手机登录信息
          result = await reqSmsLogin(phone, code)
        }else {//用户名密码登录
          const {name,pwd,captcha} = this
          // 前台表单验证
          if(!name) {
            return MessageBox.alert('用户名必须指定')
          } else if (!pwd) {
            return MessageBox.alert('密码必须指定')
          } else if (!captcha) {
            return MessageBox.alert('请您输入正确验证码')
          }
          // 发登陆的请求
          result = await reqPwdLogin({name, pwd, captcha})
        }
        // 请求结束后, 停止倒计时
        this.countdown = 0
        this.updateCaptcha()
        // this.captcha = ''
        // 根据请求结果登录跳转到个人界面
        if(result.code===0) { // 成功
          const user = result.data
          // 将user信息同步保存到state
          this.$store.dispatch('saveUser', user)
          // 跳转到个人中心界面
          this.$router.replace('/profile')
        } else { // 失败
          MessageBox.alert(result.msg)
        }

      }
    }
  } 
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../assets/stylus/minxins'
  .loginContainer
    width 100%
    height 100%
    background #fff
    .loginInner
      padding-top 60px
      width 80%
      margin 0 auto
      .login_header
        .login_logo
          font-size 40px
          font-weight bold
          color #02a774
          text-align center
        .login_header_title
          padding-top 40px
          text-align center
          >a
            color #333
            font-size 14px
            padding-bottom 4px
            &:first-child
              margin-right 40px
            &.on
              color #02a774
              font-weight 700
              border-bottom 2px solid #02a774
      .login_content
        >form
          >div
            display none
            &.on
              display block
            input
              width 100%
              height 100%
              padding-left 10px
              box-sizing border-box
              border 1px solid #ddd
              border-radius 4px
              outline 0
              font 400 14px Arial
              &:focus
                border 1px solid #02a774
            .login_message
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff
              .get_verification
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                border 0
                color #ccc
                font-size 14px
                background transparent
                &.right_phone_number
                  color black 
            .login_verification
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff
              .switch_button
                font-size 12px
                border 1px solid #ddd
                border-radius 8px
                transition background-color .3s,border-color .3s
                padding 0 6px
                width 30px
                height 16px
                line-height 16px
                color #fff
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                &.off
                  background #fff
                  .switch_text
                    float right
                    color #ddd
                &.on
                  background #02a774
                >.switch_circle
                //transform translateX(27px)
                  position absolute
                  top -1px
                  left -1px
                  width 16px
                  height 16px
                  border 1px solid #ddd
                  border-radius 50%
                  background #fff
                  box-shadow 0 2px 4px 0 rgba(0,0,0,.1)
                  transition transform .3s
                  &.right
                    transform translateX(27px)
            .login_hint
              margin-top 12px
              color #999
              font-size 14px
              line-height 20px
              >a
                color #02a774
          .login_submit
            display block
            width 100%
            height 42px
            margin-top 30px
            border-radius 4px
            background #4cd96f
            color #fff
            text-align center
            font-size 16px
            line-height 42px
            border 0
        .about_us
          display block
          font-size 12px
          margin-top 20px
          text-align center
          color #999
      .go_back
        position absolute
        top 5px
        left 5px
        width 30px
        height 30px
        >.iconfont
          font-size 20px
          color #999
</style>



